/*山坡地資訊查詢平台*/
.main {
    &[data-con="search"] {
        margin-top: 54px;
        padding-top: 64px;
        padding-bottom: 150px;
        background:
            url("../images/img-butterfly.svg") no-repeat 95% 165px,
            url("../images/footer-bg.svg") no-repeat center bottom;
        background-size: initial, cover;
        .main-title {
            /*大標題*/
            font-size: $h2-font-size;
            font-weight: $font-weight-heavy;
            color: $gray-600;
            text-align: center;
            margin-bottom: 64px;
            @include PC {
                font-size: $h3-font-size;
            }
            @include mobile-lg {
                font-size: $h4-font-size;
            }
        }
        .main-container {
            background: $white;
            border-radius: 20px;
            box-shadow: 0 3px 6px $gray-400;
            position: relative;
            padding: 40px;
            @include PC {
                padding: 32px;
            }
            @include Pad {
                padding: 24px;
            }
            @include mobile-lg {
                padding: 16px;
            }
            &:before,
            &:after {
                content: "";
                display: block;
                position: absolute;
            }
            &:before {
                background: url("../images/img-animal-top.svg");
                background-size: cover;
                width: 388px;
                height: 100px;
                top: -100px;
                left: 40px;
                @include PC {
                    width: 300px;
                    height: 77px;
                    top: -77px;
                }
                @include Pad {
                    width: 213px;
                    height: 55px;
                    top: -55px;
                }
            }
            &:after {
                background: url("../images/img-photo-top.png");
                background-size: cover;
                width: 280px;
                height: 212px;
                top: -114px;
                right: 72px;
                @include PC {
                    width: 240px;
                    height: 182px;
                    top: -90px;
                }
                @include PC-sm {
                    width: 208px;
                    height: 157px;
                    top: -74px;
                }
                @include Pad {
                    display: none;
                }
            }
            &-top {
                margin-right: 370px;
                @include PC {
                    margin-right: 300px;
                }
                @include PC-sm {
                    margin-right: 250px;
                }
                @include Pad {
                    margin-right: 0;
                }
            }
        }
    }
    &[data-item="calc"] {
        .main-container {
            border-radius: 0 0 20px 20px;
            padding-top: 30px;
            &:before,
            &:after {
                display: none;
            }
        }
        .main-container-header {
            border-radius: 20px 20px 0 0;
            color: $primary-xdark;
            align-items: center;
            padding: 12px 40px;
            position: relative;
            display: flex;
            @include Pad {
                padding: 10px 24px;
            }
            @include mobile-lg {
                padding: 10px 16px;
            }
            &:after {
                content: "";
                display: block;
                background: url("../images/img-bird-top.svg");
                background-size: cover;
                width: 436px;
                height: 77px;
                position: absolute;
                right: 24px;
                top: -77px;
                @include PC {
                    width: 365px;
                    height: 66px;
                    top: -66px;
                }
                @include PC-sm {
                    width: 311px;
                    height: 56px;
                    top: -56px;
                }
                @include Pad {
                    width: 290px;
                    height: 52px;
                    top: -52px;
                }
            }
            img {
                width: 32px;
                @include PC {
                    width: 24px;
                }
            }
        }
        .main-container-title {
            display: flex;
            font-size: $h4-font-size;
            font-weight: $font-weight-bold;
            margin-bottom: 0;
            @include PC {
                font-size: $h5-font-size;
            }
        }
    }
}
/*--------------------主選單開始------------------*/
.navbar[data-con="search"] {
    .navbar-brand {
        position: relative;
        z-index: 100;
        height: 54px;
    }
    .navbar-collapse {
        background: $tertiary-light;
        top: 0;
        @include PC-sm {
            top: 54px;
        }
        .navbar-main {
            .nav-item{
                &:nth-last-child(3) {
                &::after {
                    display: block;
                    @include PC-sm {
                        display: none !important;
                    }
                }
            }
            }
            .nav-link {
                color: $primary-dark;
                @include PC-sm {
                    font-size: $h6-font-size;
                }
            }
        }
    }
}
