.key {
    color: $gray-300;
    font-size: $h8-font-size;
}
/*-----------------標題樣式---------------------*/
.sub-title {
    font-size: $h5-font-size;
    border-bottom: 1px solid $primary;
    font-weight: $font-weight-bold;
    padding-bottom: 8px;
    display: flex;
    align-items: center;
    @include Pad {
        font-size: $h6-font-size;
        line-height: 1.5;
    }
    img{
        width: 32px;
        margin-right: 8px;
    }
}
/*-----------------連結樣式---------------------*/
.link-img {
    display: inline-block;
    &:hover {
        opacity: 0.8;
    }
}
.link-list{
    display: flex;
    @include mobile-lg{
        flex-direction: column;
    }
    &-item{
        border-right: 1px solid $gray-400;
        @include mobile-lg{
            border-right: none;
            border-bottom: 1px solid $gray-400;
        }
        &:last-child{
            border-right: 0;
            @include mobile-lg{
                border-bottom: none;
            }
        }
    }
    &-link{
        display: block;
        font-weight: $font-weight-medium;
        padding: 0 16px;
        @include Pad{
            font-size: $h7-font-size;
        }
        @include mobile-lg{
            padding: 4px 0;
        }
    }
}
/*-----------------頁籤樣式---------------------*/
.nav-tabs{
    .nav-item{
        margin-bottom: 0;
    }
    .nav-link{
        background: $primary-light;
        color: $primary-dark;
        border-radius: 8px;
        padding: 4px 40px !important;
        @include PC{
            padding: 4px 32px !important;
        }
        &.active{
            background: $primary-dark;
            color: $white;
        }
    }
}
/*-----------------按鈕樣式---------------------*/
.radio-list{
    display: flex;
    flex-wrap: wrap;
    &-item{
        margin-right: 12px;
        margin-bottom: 12px;
        &:last-child{
            margin-right: 0;
        }
    }
    &-btn{
        border: 1px solid $primary;
        box-shadow: 0 2px 2px $gray-500;
        @include Pad{
            font-size: $h7-font-size;
        }
        &:hover{
            border: 1px solid $primary;
            box-shadow: none;
        }
        &.active{
            color: $secondary-xdark;
            background: $secondary;
            box-shadow: none;
            border: none;
        }
    }
}
.shadow-link{
    &:hover{
        box-shadow: none !important;
    }
}
.opacity-link{
    &:hover{
        opacity: 0.8;
    }
}
/*-----------------列表樣式---------------------*/
.text-list{
    display: flex;
    @include PC-sm{
        flex-direction: column;
    }
    &-item{
        display: flex !important;
        font-size: $h7-font-size;
        align-items: flex-start;
    }
}
.dot-list{
    &-item{
        display: flex;
        align-items: start;
        font-size: $h7-font-size;
        margin-bottom: 4px;
        &::before{
            content: "";
            display: inline-block;
            width: 7px;
            height: 7px;
            background: $danger;
            margin-right: 12px;
            flex-shrink: 0;
            margin-top: 8px;
        }
    }
}
.tag-list {
    background: $white;
    &-item {
        border-bottom: 1px solid $primary-light;
        &:last-child {
            border-bottom: none;
        }
    }
    &-link {
        display: flex !important;
        align-items: start;
        color: $gray-800;
        padding: 16px 0;
        font-size: $h6-font-size;
        @include PC-sm {
            padding: 12px 0;
        }
        @include mobile-lg {
            font-size: $h7-font-size;
        }
        &:before {
            content: "";
            display: block;
            width: 7px;
            height: 17px;
            margin-right: 16px;
            margin-top: 2px;
            background: $tertiary;
            flex-shrink: 0;
            @include mobile-lg {
                margin-right: 12px;
            }
        }
        &:hover {
            text-decoration: underline !important;
        }
        p {
            @include over_text(2);
        }
    }
}
.icon-list {
    display: flex;
    @include Pad {
        flex-direction: column;
    }
    &-item {
        display: flex;
        margin-right: 16px;
        @include Pad {
            margin-bottom: 8px;
        }
        &:last-child {
            margin-right: 0;
            margin-bottom: 0;
        }
        img {
            width: 20px;
            margin-right: 8px;
        }
    }
}
/*-----------------表格樣式---------------------*/
.table-rwd {
    min-width: 100%;
    @include PC-sm{
        min-width: 90%;
        margin: 0 auto;
    }
    tr{
        border-bottom: 1px solid $primary-light;
        @include PC-sm{
            border: 1px solid $primary;
            border-top: 8px solid $primary;
        }
    }
    th {
        display: table-cell;
        background: $primary;
        color: $white;
        font-weight: normal;
        vertical-align: middle;
        font-size: $h6-font-size;
        padding: 8px;
        @include PC-sm {
            display: none;
        }
    }
    td {
        display: table-cell;
        vertical-align: middle;
        padding: 16px 8px;
        @include PC-sm {
            display: flex;
            text-align: left !important;
            align-items: start;
            padding: 4px 20px !important;
            position: relative;
            font-size: $h7-font-size;
            &[data-th=""] {
                &::before {
                    content: "";
                    display: none;
                }
            }
        }
        &:first-child{
            @include PC-sm {
                padding-top: 16px !important;
            }
        }
        &:last-child{
            @include PC-sm {
                padding-bottom: 16px !important;
            }
        }
        &:before {
            display: none;
            @include PC-sm {
                flex-shrink: 0;
                content: attr(data-th) "：";
                display: inline-block;
                color: $primary;
                font-weight: $font-weight-medium;
            }
        }
    }
    a{
        color: $bright;
        &:hover{
            opacity: 0.8;
        }
    }
}
.table-group {
    display: flex;
    align-items: center;
    width: 100%;
    @include mobile-md {
        flex-direction: column;
        align-items: start;
    }
}
.table-sign {
    margin: 0 auto;
    @include PC-sm {
        margin: 0;
        display: inline-block;
    }
}

/*-----------------卷軸樣式---------------------*/
.scrollbar {
    width: 100%;
    overflow: auto;
    position: relative;
    -webkit-overflow-scrolling: touch;
    &::-webkit-scrollbar-track {
        background-color: $gray-300;
    }
    &::-webkit-scrollbar {
        width: 4px;
        height: 4px;
        background-color: $gray-300;
    }
    &::-webkit-scrollbar-thumb {
        border-radius: 10px;
        background-color: $gray-500;
    }
}
/*-----------------表單樣式---------------------*/
.input-search {
    display: flex;
    align-items: center;
    label {
        font-size: $h5-font-size;
    }
    input {
        border-radius: 50px 0 0 50px !important;
        border: 1px solid $primary !important;
        border-right: 0 !important;
        padding-left: 20px;
        height: 45px;
        @include PC-sm {
            height: 34px;
        }
        &:focus {
            box-shadow: none;
        }
        &::placeholder {
            color: $gray-600 !important;
            @include PC-sm {
                font-size: $h7-font-size;
            }
        }
    }
    input ~ button {
        border-radius: 0 50px 50px 0 !important;
        border: 1px solid $primary;
        border-left: 0;
        background: $white !important;
        height: 45px;
        @include PC-sm {
            height: 34px;
        }
        &:hover {
            img {
                opacity: 0.8;
            }
        }
    }
}
/*-----------------視窗樣式---------------------*/
.modal {
    &-header {
        align-items: flex-start;
        border-bottom: none;
        position: relative;
        font-size: $h5-font-size;
        font-weight: $font-weight-semibold;
        padding-left: 24px;
        padding-right: 24px;
        &:after{
            content: "";
            display: block;
            position: absolute;
            left: 24px;
            bottom: 0;
            width: calc(100% - 48px);
            height: 1px;
            background: $primary-light;
        }
        .btn-close{
            background: url("../images/ic-close.svg") no-repeat center;
            background-size: 80%;
            opacity: 1;
            @include text-indent;
            &:hover{
                opacity: 0.8;
            }
        }
    }
    &-content{
        border-radius: 16px !important;
    }
    &-body {
        font-size: $h6-font-size;
        @include mobile-lg {
            font-size: $h7-font-size;
        }
    }
}
/*-----------------手風琴樣式---------------------*/
.accordion {
    &-body {
        position: relative;
        @include Pad {
            font-size: $h6-font-size;
        }
        &::before {
            content: "";
            display: block;
            position: absolute;
            width: calc(100% - 32px);
            height: 1px;
            left: 16px;
            top: 0;
            background: $primary-light;
        }
    }
    &-item {
        border: none;
    }
    &-button {
        background: $white;
        color: $primary;
        border-radius: 8px !important;
        padding: 12px 48px 12px 32px;
        position: relative;
        line-height: 1.3;
        box-shadow: none !important;
        font-size: $h5-font-size;
        font-weight: $font-weight-medium;
        @include Pad {
            padding: 12px 32px 12px 18px;
        }
        &:after {
            background: url(../images/ic-add-active.svg);
            background-size: 100%;
            position: absolute;
            right: 32px;
            top: calc(50% - 10px);
            width: 24px;
            height: 24px;
            @include Pad {
                right: 16px;
                width: 18px;
                height: 18px;
                top: calc(50% - 9px);
            }
        }
        &:not(.collapsed) {
            background: $white;
            color: $secondary;
        }
        &:not(.collapsed)::after {
            background: url(../images/ic-minus-default.svg);
            background-size: 100%;
            transform: rotate(0deg);
        }
    }
}
/*-----------------頁數樣式---------------------*/

.page {
    display: flex;
    justify-content: center;
    align-items: center;
    @include mobile-lg {
        flex-direction: column;
        align-items: center;
    }
    &-list {
        display: flex;
        justify-content: flex-end;
        border-right: 1px solid $gray-300;
        padding-right: 16px;
        margin-right: 16px;
        @include mobile-lg {
            margin-bottom: 12px;
            border-right: none;
            padding-right: 0;
            margin-right: 0;
        }
    }
    &-item {
        padding: 0 4px;
        display: flex;
        align-items: center;
        &:nth-child(1),
        &:nth-child(2),
        &:nth-last-child(1),
        &:nth-last-child(2) {
            .page-link {
                border: 1px solid $gray-200;
                background: $gray-100;
            }
        }
        .form-control {
            width: 40px;
            height: 28px;
            color: $primary;
            border: 1px solid $primary;
            border-radius: 5px;
            margin: 0 5px;
            padding: 0 8px;
            text-align: center;
        }
    }
    &-link {
        border-radius: 4px;
        background: $white;
        border: 1px solid $primary;
        color: $primary;
        padding: 0;
        width: 28px;
        height: 28px;
        display: flex;
        justify-content: center;
        align-items: center;
        transition: all 0.2s ease;
        &:hover {
            background: $gray-100;
        }
        &.active {
            border: 2px solid $primary;
            font-weight: $font-weight-medium;
        }
        img {
            width: 25px;
            margin-right: 0;
        }
        &-function {
            border-radius: 4px;
        }
    }
    &-form {
        display: flex;
        align-items: center;
        flex-shrink: 0;
        color: $gray-600;
        span {
            flex-shrink: 0;
        }
        select {
            width: 64px;
            height: 28px;
            flex-shrink: 0;
            padding-top: 0;
            padding-bottom: 0;
        }
    }
}
/*-----------------容器樣式---------------------*/
.file-container{
    display: flex;
    align-items: stretch;
    border-radius: 8px;
    border: 1px solid $secondary-xdark;
    padding: 4px;
    background: $white;
    width: 100%;
    span{
        background: $secondary-xdark;
        border-radius: 5px;
        padding: 0 8px;
        line-height: 0;
        color: $white;
        margin-right: 12px;
        display: flex;
        align-items: center;
    }
    p{
        font-size: $h7-font-size;
    }
}