.section {
	margin-bottom: 70px;
	@include Pad {
		margin-bottom: 48px;
	}
	&-text {
		@include Pad {
			font-size: $h8-font-size;
		}
	}
}
.container {
	width: 90%;
	max-width: 1150px;
	@include PC-sm {
		width: 95%;
		max-width: 1920px;
	}
	@include mobile-md {
		max-width: 98%;
	}
}
.main {
	margin-top: 102px;
	@include PC {
		margin-top: 56px;
	}
	@include PC-sm {
		margin-top: 49px;
	}
	@include mobile-lg {
		margin-top: 49px;
	}
}
/*--------------------主選單開始------------------*/
.navbar {
	background: $white;
	height: 54px;
	justify-content: center;
	width: 100%;
	&-brand {
		color: $gray-700;
		display: flex;
		align-items: center;
		letter-spacing: 3px;
		h1 {
			font-size: $h5-font-size;
			@include mobile-lg {
				font-size: $h6-font-size;
			}
		}
	}
	&-toggler {
		border: none;
		width: 30px;
		height: 30px;
		@include text-indent;
		background: url("../images/ic-close.svg") no-repeat center;
		background-size: cover;
		&.collapsed {
			background: url("../images/ic-menu.svg") no-repeat center;
			background-size: cover;
		}
	}
	&-collapse {
		position: fixed;
		left: 0;
		top: 54px;
		width: 100%;
		background: $tertiary;
		@include PC-sm {
			box-shadow: 0 4px 3px $gray-500;
			left: 0;
			width: 100%;
		}
	}
	&-main {
		/*主選單*/
		width: 100%;
		.nav-item {
			position: relative;
			@include PC-sm {
				width: 100%;
			}
			&:after {
				position: absolute;
				content: "";
				width: 1px;
				height: calc(100% - 24px);
				right: 0;
				top: 12px;
				background: $white;
				@include PC-sm {
					display: none;
				}
			}
			&:nth-last-child(3){
				&::after {
					display: none;
					@include PC-sm{
						display: block;
					}
				}
			}
			&:last-child {
				&::after {
					display: none;
				}
				.nav-link {
					padding-right: 0 !important;
					@include PC-sm {
						padding-right: 32px !important;
					}
				}
			}
		}
		.nav-link {
			padding: 12px 40px !important;
			font-size: $h5-font-size;
			font-weight: $font-weight-bold;
			@include PC {
				padding: 12px 32px !important;
			}
			@include PC-sm{
				font-size: $h6-font-size;
			}
			&:hover {
				color: $black;
			}
		}
	}
}

.key {
	position: fixed;
	left: 4px;
	top: 8px;
}
.nav {
	&-item {
		display: flex;
		align-items: center;
		position: relative;
		@include PC {
			font-size: $h7-font-size;
		}
		@include PC-sm {
			border-bottom: 1px solid $primary-light;
		}
		&:last-child,
		&:nth-last-child(2),
		&:nth-last-child(3) {
			&::before {
				display: none;
			}
		}
		&:before {
			content: "";
			display: block;
			width: 1px;
			height: 24px;
			background: $gray-300;
			position: absolute;
			right: 0;
			top: calc(50% - 12px);
			@include PC-sm {
				display: none;
			}
		}
	}
	&-link {
		display: block;
		color: $gray-600;
		padding: 12px 20px !important;
		position: relative;
		font-weight: $font-weight-medium;
		@include PC {
			padding: 16px 10px !important;
			font-size: $h7-font-size;
		}
		@include PC-sm {
			width: 100%;
			padding: 12px 16px !important;
			text-align: center;
		}
		&:hover {
			color: $primary;
		}
	}
}
/*--------------------footer開始------------------*/
.footer {
	background: $secondary-xlight;
	position: relative;
	padding: 40px 0;
	margin-top: -1px;
	&-start {
		width: 100%;
		margin-right: 72px;
		@include PC {
			margin-right: 0;
		}
	}
	&-end {
		width: 180px;
		display: flex;
		flex-shrink: 0;
		flex-direction: column;
		align-items: stretch;
		@include PC {
			width: 100%;
			flex-direction: row;
			align-items: center;
		}
		@include mobile-lg {
			flex-direction: column;
			align-items: flex-start;
			& > * {
				margin-bottom: 12px;
			}
		}
	}
	&-btn {
		background: $white !important;
		color: $primary-dark;
		justify-content: flex-start;
		margin-bottom: 16px;
		@include Pad {
			font-size: $h7-font-size;
			padding: 2px 16px;
			text-align: left;
		}
		@include mobile-lg {
			margin-bottom: 12px;
			width: 100%;
			justify-content: space-between;
		}
		&:hover {
			border: 1px solid $primary;
		}
	}
	&-function {
		display: flex;
		flex-direction: column;
		position: fixed;
		right: 12px;
		bottom: 16px;
		z-index: 100;
		&-link {
			width: 48px;
			height: 48px;
			border-radius: 50%;
			display: flex;
			justify-content: center;
			align-items: center;
			font-weight: $font-weight-semibold;
			font-size: $h8-font-size;
			color: $white;
			background: $primary;
			@include mobile-lg {
				width: 40px;
				height: 40px;
			}
			&:hover {
				background-color: $white;
				color: $primary;
			}
		}
	}
	.key {
		position: absolute;
		left: 16px;
		top: 16px;
	}
}

/*--------------------內容開始------------------*/

/*--------------------視窗開始------------------*/
.backdrop {
	width: 100vw;
	height: 100vh;
	position: absolute;
	left: 0;
	top: 0;
	display: none;
	z-index: 100;
	&.show {
		display: block;
	}
}
.offcanvas-backdrop.show {
	opacity: 0.7;
}
.offcanvas-header {
	padding-left: 20px;
	padding-right: 32px;
	.btn {
		&:active,
		&:focus {
			border: 0;
		}
	}
}
.offcanvas-bottom {
	border-radius: 24px 24px 0 0;
	height: 350px !important;
	padding: 24px 0;
	.offcanvas-header {
		position: relative;
		&-close {
			position: absolute;
			background: $gray-100 !important;
			border-radius: 24px 24px 0 0;
			width: 106px;
			height: 40px;
			top: -39px;
			left: calc(50% - 53px);
			display: flex;
			justify-content: center;
			align-items: center;
			border: none;
			&::before,
			&::after {
				content: "";
				display: block;
				position: absolute;
				width: 24px;
				height: 24px;
				background: url("../images/fillet-gray.svg");
			}
			&::before {
				bottom: -1px;
				left: -23px;
			}
			&::after {
				bottom: -1px;
				right: -23px;
				transform: rotate(90deg);
			}
			&:focus {
				background: $gray-100;
			}
		}
	}
	.offcanvas-body {
		padding: 0 66px;
		overflow-y: initial;
	}
}
.offcanvas-end {
	width: 350px !important;
	border-radius: 24px 0 0 24px;
	.offcanvas-header {
		border-radius: 24px 0 0 0;
	}
}
.offcanvas-body {
	padding: 0 32px 24px 32px;
}

.select-list-wrapper {
	overflow-y: auto;
	padding: 0 32px 24px 32px;
	flex: 1 1;
	font-size: $h6-font-size;
	.form-check-input {
		width: 16px;
		height: 16px;
		margin-top: 0;
		border: 2px solid $gray-700 !important;
		&[type="radio"] {
			border-radius: 50%;
		}
		&:checked[type="radio"] {
			border-radius: 50%;
			background: url(../images/radio-circle.svg) no-repeat center $primary;
			background-size: 6px;
			border: 2px solid $primary !important;
		}
		&[type="checkbox"] {
			border-radius: 4;
		}
		&:checked[type="checkbox"] {
			background: url(../images/ic-check.svg) no-repeat center $primary;
			background-size: 9px;
			border: 2px solid $primary !important;
		}
	}
	input[disabled] {
		border: 2px solid $gray-600;
	}
	input[disabled] ~ label {
		opacity: 1 !important;
		color: $gray-700;
	}
	input[disabled] ~ label span {
		color: $danger !important;
	}
	label {
		display: flex;
		justify-content: space-between;
		padding-left: 4;
		p {
			display: block;
			width: calc(100% - 70px);
			flex: 1 1;
			line-height: 1.3;
		}
		span {
			padding-left: 8px;
		}
	}
}
